import React from 'react';
import './Body.scss';

import BannerLogo from './images/banner-logo.png';

import ReadyMadeTopLeftLogo from './images/ready-made-top-left-logo.png';
import ReadyMadeTopRightLogo from './images/ready-made-top-right-logo.png';
import ReadyMadeBottomLeftLogo from './images/ready-made-bottom-left-logo.png';
import ReadyMadeBottomRightLogo from './images/ready-made-bottom-right-logo.png';

import PromoTopLeftLogo from './images/promo-top-left-logo.png';
import PromoTopRightLogo from './images/promo-top-right-logo.png';
